"use client"

import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Select } from "@/components/ui/select"
import { Button } from "@/components/ui/button"
import { useState } from "react"

export default function LaunchPage() {
  const [buyerWallets, setBuyerWallets] = useState<string[]>([])
  const [totalBuyAmount, setTotalBuyAmount] = useState("1")

  const addWallet = () => {
    setBuyerWallets([...buyerWallets, ""])
  }

  const deleteWallet = (index: number) => {
    const newWallets = buyerWallets.filter((_, i) => i !== index)
    setBuyerWallets(newWallets)
  }

  const equalBuy = () => {
    const amount = parseFloat(totalBuyAmount) / buyerWallets.length
    const newWallets = buyerWallets.map(() => amount.toFixed(8))
    setBuyerWallets(newWallets)
  }

  const randomBuy = () => {
    const total = parseFloat(totalBuyAmount)
    let remaining = total
    const newWallets = buyerWallets.map((_, index) => {
      if (index === buyerWallets.length - 1) {
        return remaining.toFixed(8)
      }
      const amount = Math.random() * (remaining / 2)
      remaining -= amount
      return amount.toFixed(8)
    })
    setBuyerWallets(newWallets)
  }

  return (
    <div className="space-y-6">
      <Card className="p-6 border-2 border-blue-500">
        <h2 className="text-lg font-semibold mb-4">1️⃣ Select Token to Launch</h2>
        <Select>
          <option>Donald Trump (DNT) - CoJUvBkduCa8bUCk4MjvGdczTYpw7MJGJJ77zUFuKQ</option>
        </Select>
        <div className="mt-4 grid grid-cols-2 gap-4">
          <div>
            <p className="text-sm text-gray-500">Name: Donald Trump</p>
            <p className="text-sm text-gray-500">Symbol: DNT</p>
            <p className="text-sm text-gray-500">Description: MAGA DEGEN GOGOGO</p>
            <p className="text-sm text-gray-500">Website: null</p>
          </div>
          <div>
            <p className="text-sm text-gray-500">Telegram: null</p>
            <p className="text-sm text-gray-500">Twitter: null</p>
            <p className="text-sm text-gray-500 break-all">Address: CoJUvBkduCa8bUCk4MjvGdczTYpw7MJGJJ77zUFuKQ</p>
          </div>
        </div>
      </Card>

      <Card className="p-6 border-2 border-blue-500">
        <h2 className="text-lg font-semibold mb-4">2️⃣ Select Funding Wallet</h2>
        <Select>
          <option>Balance: 0.006002000 SOL (1.24 USDT) - G92h3wp62bHyqoGyXR8fgiXUkLFvwWFHvHKtokApBCxq</option>
        </Select>
      </Card>

      <Card className="p-6 border-2 border-blue-500">
        <h2 className="text-lg font-semibold mb-4">3️⃣ Select Dev Wallet</h2>
        <p className="text-sm text-gray-500 mb-2">Dev address</p>
        <Select>
          <option>Random address</option>
        </Select>
      </Card>

      <Card className="p-6 border-2 border-blue-500">
        <h2 className="text-lg font-semibold mb-4">4️⃣ Add Buyer Wallets</h2>
        <div className="space-y-4">
          <div className="flex gap-4 items-center">
            <span className="text-sm text-gray-500">Total buy amount (SOL)</span>
            <Input 
              value={totalBuyAmount}
              onChange={(e) => setTotalBuyAmount(e.target.value)}
              className="max-w-[200px]"
            />
          </div>
          
          <div className="flex gap-2">
            <Button onClick={addWallet} variant="default">Add wallet</Button>
            <Button onClick={() => setBuyerWallets([])} variant="destructive">Delete wallet</Button>
            <Button onClick={equalBuy} variant="outline">Equal buy</Button>
            <Button onClick={randomBuy} variant="outline">Random buy</Button>
          </div>

          <div className="space-y-2">
            {buyerWallets.map((wallet, index) => (
              <div key={index} className="flex gap-2 items-center">
                <span className="w-6">{index + 1}</span>
                <Input 
                  value={wallet}
                  onChange={(e) => {
                    const newWallets = [...buyerWallets]
                    newWallets[index] = e.target.value
                    setBuyerWallets(newWallets)
                  }}
                />
              </div>
            ))}
          </div>
        </div>
      </Card>
    </div>
  )
}
